import React from 'react';
import { createStyles, Theme, makeStyles, fade } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Divider from '@material-ui/core/Divider';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import OpenInNewIcon from '@material-ui/icons/OpenInNew';
import IconButton from '@material-ui/core/IconButton';
import Popover from '@material-ui/core/Popover';
import Button from '@material-ui/core/Button';
import PictureListData from "./PictureListData";
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: '100%',
    },
    inline: {
      display: 'inline',
    },
    iconButton: {
        position: "absolute",
        bottom: 0,
        right: 0,
        marginBottom: theme.spacing(-3),
    },typography: {
        padding: theme.spacing(2),
      },
  }),
);

export default function PictureList({theme,data}) {
  const classes = useStyles();
  const [item, setItems] = React.useState(0);
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
    setItems(event.currentTarget.value);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);
  const id = open ? 'simple-popover' : undefined;

  return (
    <List className={classes.root}>
      { data.map((obj, index) =>  
          index<3 && (
          <div>
      <ListItem alignItems="flex-start">
        <ListItemText
          secondary={
            <React.Fragment >
              <Typography
                component="span"
                variant="body2"
                color="textPrimary"
              >
               {obj.title} วันที่ {obj.date.slice(0, obj.date.length-4)}{parseInt(obj.date.slice(obj.date.length-4,obj.date.length))+543}
              </Typography>
              <IconButton aria-label="share" id={index} value={index} className={classes.iconButton} onClick={handleClick}>
              <Typography
                component="span"
                variant="button"
                color="textSecondary"
              >
                  อ่านต่อ
                  </Typography>
        </IconButton> 
            </React.Fragment>
          }
        />
      </ListItem>
      <Divider variant="inset" component="li" style={{marginTop: theme.spacing(1)}}/>
      </div>
     ))}
     <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
            vertical: 'center',
            horizontal: 'left',
          }}
          transformOrigin={{
            vertical: 'center',
            horizontal: 'right',
          }}
      >
          <PictureListData theme={theme} obj={data[item]}></PictureListData>
      </Popover>
    </List>
  );
}