import React from 'react';
import { createStyles, Theme, withStyles, WithStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import { TransitionProps } from '@material-ui/core';
import Slide from '@material-ui/core/Slide';
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import FavoriteCard from "./FavoriteCard";
import MultipleSelectAll from "./MultipleSelectAll";
import PictureListData from './PictureListData'

const Transition = React.forwardRef(function Transition(
    props: TransitionProps & { children?: React.ReactElement<any, any> },
    ref: React.Ref<unknown>,
  ) {
    return <Slide direction="up" ref={ref} {...props} />;
  });

const styles = (theme: Theme) =>
  createStyles({
    root: {
      margin: 0,
      padding: theme.spacing(2),
      backgroundColor: theme.palette.primary.dark,
    },
    closeButton: {
      position: 'absolute',
      right: theme.spacing(1),
      top: theme.spacing(1),
      color: theme.palette.grey[500],
    },
  });

export interface DialogTitleProps extends WithStyles<typeof styles> {
  id: string;
  children: React.ReactNode;
  onClose: () => void;
}

const DialogTitle = withStyles(styles)((props: DialogTitleProps) => {
  const { children, classes, onClose, data, setRenderData, isAll, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}
          <MultipleSelectAll data={data} perpage={30} setRenderData={setRenderData} isAll={isAll}></MultipleSelectAll></Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme: Theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

const DialogActions = withStyles((theme: Theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(1),
  },
}))(MuiDialogActions);

export default function ListItemDialog({theme, data,title, isOpenAll,setOpenDialogAll,isAll}) {
  const [open, setOpen] = React.useState(isOpenAll);
  const [renderData, setRenderData] = React.useState([]);

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
    setOpenDialogAll(false);
  };

  return (
    <div>
      <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" 
      open={open}
      fullScreen={true}
      TransitionComponent={Transition}
      keepMounted>
        <DialogTitle id="customized-dialog-title" onClose={handleClose} data={data} setRenderData={setRenderData} isAll={isAll}>
          {title}
        </DialogTitle>
        <DialogContent dividers>
      { renderData &&  (
        renderData.map((obj, index) => 
          
                <Paper>
                <PictureListData theme={theme} obj={obj} ></PictureListData>
                </Paper>
                
      ))}
        </DialogContent>
       {/* <DialogActions>
          <Button autoFocus onClick={handleClose} color="primary">
            Save changes
          </Button>
       </DialogActions> */}
      </Dialog>
    </div>
  );
}