import React from 'react';

import axios from "axios";
import { makeStyles, useTheme } from '@material-ui/core/styles';
import MobileStepper from '@material-ui/core/MobileStepper';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight';
import SwipeableViews from 'react-swipeable-views';
import {gallery_list} from "./data/personal_data";
import { autoPlay } from 'react-swipeable-views-utils';
import CircularProgress from '@material-ui/core/CircularProgress';

import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';

import { CardActionArea } from "@material-ui/core";
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import Fade from '@material-ui/core/Fade';

const AutoPlaySwipeableViews = autoPlay(SwipeableViews);

const tutorialSteps = [
  {
    label: 'San Francisco – Oakland Bay Bridge, United States',
    imgPath:
      'https://images.unsplash.com/photo-1537944434965-cf4679d1a598?auto=format&fit=crop&w=400&h=250&q=60',
  },
  {
    label: 'Bird',
    imgPath:
      'https://images.unsplash.com/photo-1538032746644-0212e812a9e7?auto=format&fit=crop&w=400&h=250&q=60',
  },
  {
    label: 'Bali, Indonesia',
    imgPath:
      'https://images.unsplash.com/photo-1537996194471-e657df975ab4?auto=format&fit=crop&w=400&h=250&q=80',
  },
  {
    label: 'NeONBRAND Digital Marketing, Las Vegas, United States',
    imgPath:
      'https://images.unsplash.com/photo-1518732714860-b62714ce0c59?auto=format&fit=crop&w=400&h=250&q=60',
  },
  {
    label: 'Goč, Serbia',
    imgPath:
      'https://images.unsplash.com/photo-1512341689857-198e7e2f3ca8?auto=format&fit=crop&w=400&h=250&q=60',
  },
];

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    width: '100%',
    backgroundColor: theme.palette.secondary.light,
  },
  header: {
    display: 'flex',
    alignItems: 'center',
    height: 50,
    paddingLeft: theme.spacing(4),
    backgroundColor: theme.palette.secondary.main,
  },
  img: {
    display: "flex",
    flexDirection: "column",
    alignContent: "center",
    justifyContent: "center",    
    justifyItems: "center",
    overflow: 'hidden',
    width: '100%',
  },
  gridList: {
    width: "100%",
    height: "60vh",
    overflow: "scroll"
  },
}));

function SwipeableTextMobileStepper({title, index, theme}) {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const [maxSteps, setMaxSteps] = React.useState(0);
  const [loading, setLoading] = React.useState(false);

  const [images, setImages] = React.useState(null);
  const [open, setOpen] = React.useState(false);
  const [openImage, setOpenImage]  = React.useState(null);


  const handleClickOpen = (event) => {
    setOpen(true);
    console.log(event);
    setOpenImage(event.target.src);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleClickDownloadG = (event) => {
    window.open(event,'_blank');
}

  const handleClickDownloadT = (event) => {
    var str = event.target.src; 
    var sli = str.slice(0,str.length-4);
    var res = "w2048";
    window.open(sli.concat(res),'_blank');
    console.log(res);
}


  const handleClickDownload = (event) => {
    var str = event.target.src; 
    var sli = str.slice(0,str.length-5);
    var res = "w2048";
    window.open(sli.concat(res),'_blank');
    console.log(res);
}


  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleStepChange = (step: number) => {
    setActiveStep(step);
  };

  React.useEffect(() => {
    let shouldCancel = false;

    const call = async () => {
      const response = await axios.get(
        gallery_list[index]
      );
      if (!shouldCancel && response.data && response.data.length > 0) {
        setImages(
          response.data.map(url => ({
            original: `${url}`,
            thumbnail: `${url}=w100`,
            hiThumbnail: `${url}=w600`
          }))
        );
      }
      setMaxSteps(response.data.length);
      setLoading(true);
    };
    call();
    return () => (shouldCancel = true);
  }, []);

  return (
    <div className={classes.root}>
      <Paper square elevation={0} className={classes.header}>
          {title}
      </Paper>
      <AutoPlaySwipeableViews
        axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
        index={activeStep}
        onChangeIndex={handleStepChange}
        enableMouseEvents
      >
        {loading ? (images.map((obj, index) => (
          <div key={obj}>
            {Math.abs(activeStep - index) <= 2 ? (
              <img className={classes.img} src={obj.hiThumbnail} alt="" onClick={handleClickOpen}/>
            ) : null}
          </div>
        ))):
        <div style={{width: "100%",
        height: "200px",
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      justifyContent: "center",
    }}>
        <Fade
          in={loading}
          style={{
            transitionDelay: loading ? '800ms' : '0ms',
            color: theme.palette.secondary.main
          }}
          unmountOnExit
        >
          <CircularProgress />
        </Fade>
      </div>}
      {loading && (<Dialog style={{width: "100%"}}
        open={open}
        onClose={handleClose}
      >
      <CardActionArea >
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
          <IconButton aria-label="share">
          <Typography variant="subtitle1" color="textSecondary">
            คลิกที่รูปภาพเพื่อดาวน์โหลด
          </Typography>
        </IconButton> 
        <GridList cellHeight={100} className={classes.gridList} cols={3}>
        {images.map((tile) => (
          <GridListTile key={tile.original} cols={tile.cols || 1} 
          onClick={()=>{handleClickDownloadG(tile.original)}}>
            <img src={tile.hiThumbnail} alt="" />
          </GridListTile>
        ))}
      </GridList>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary" autoFocus>
            ปิด
          </Button>
        </DialogActions>
        </CardActionArea>
      </Dialog>
        )}
      </AutoPlaySwipeableViews>
      <MobileStepper style={{backgroundColor: theme.palette.secondary.light}}
        steps={maxSteps}
        position="static"
        variant="text"
        activeStep={activeStep}
        nextButton={
          <Button size="small" onClick={handleNext} disabled={activeStep === maxSteps - 1}>
            Next
            {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
          </Button>
        }
        backButton={
          <Button size="small" onClick={handleBack} disabled={activeStep === 0}>
            {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
            Back
          </Button>
        }
      />
    </div>
  );
}

export default SwipeableTextMobileStepper;