import React , {useEffect} from 'react';
import SwipeableViews from 'react-swipeable-views';
import { makeStyles, Theme, useTheme } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import {menu_items} from './renderdata'
import FilesCard from './filesCard';

import Button from "@material-ui/core/Button";
import MultipleSelect from "./MultipleSelect";
import Paper from "@material-ui/core/Paper";
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight';
import { autoPlay } from 'react-swipeable-views-utils';
import MobileStepper from '@material-ui/core/MobileStepper';
interface TabPanelProps {
  children?: React.ReactNode;
  dir?: string;
  index: any;
  value: any;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
}

function a11yProps(index: any) {
  return {
    id: `full-width-tab-${index}`,
    'aria-controls': `full-width-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    backgroundColor: theme.palette.background.paper,
    width: "100%",
    height: "100%",
  },
}));

export default function FullWidthTabs({orderData,teachingData,plcData}) {
  const classes = useStyles();
  const theme = useTheme();
  
  const [renderData, setRenderData] = React.useState([]);
  const [value, setValue] = React.useState(0);

  const [activeStep, setActiveStep] = React.useState(0);
  const maxSteps = renderData.length;


  const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
    setValue(newValue);
    setRenderData([]);
    setActiveStep(0);
  };

  const handleChangeIndex = (index: number) => {
    setValue(index);
  };


  useEffect(() => {
    // Run! Like go get some data from an API.
    console.log(orderData);
    console.log(teachingData);
    console.log(plcData);
  }, []);

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleStepChange = (step: number) => {
    setActiveStep(step);
  };

  return (
    <div className={classes.root}>
      <AppBar position="static" color="primary">
        <Tabs style={{backgroundColor: theme.palette.primary.main}}
          value={value}
          onChange={handleChange}
          indicatorColor="secondary"
          textColor="textPrimary"
          variant="fullWidth"
          aria-label="full width tabs example"
        >
          <Tab label={menu_items[31].title} {...a11yProps(0)} />
          <Tab label={menu_items[32].title} {...a11yProps(1)} />
          <Tab label={menu_items[33].title} {...a11yProps(2)} />
        </Tabs>
      </AppBar>
      <SwipeableViews
        axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
        index={value}
        onChangeIndex={handleChangeIndex}
      >
        <TabPanel value={value} index={0} dir={theme.direction}>
      {orderData && ( 
        <MultipleSelect data={orderData} perpage={4} setRenderData={setRenderData} setActiveStep={setActiveStep}></MultipleSelect>
      )}
        {renderData && renderData.length>0 ? (
            renderData[activeStep].map((obj,index) => (
                <FilesCard theme={theme} type={0} obj={obj} index={index}></FilesCard>
          ))
            ):(
                
                <Typography gutterBottom variant="subtitle2" component="h6" color="textPrimary" style={{width: "100%", marginTop: theme.spacing(10)}}>
                      ไม่พบ {menu_items[31].title}
                    </Typography>
            )
        }
        {renderData && renderData.length>0 && (
        <MobileStepper
        style={{background: "transparent", backgroundColor: theme.palette.primary.main ,}}
                steps={maxSteps}
                position="static"
                variant="text"
                activeStep={activeStep}
                nextButton={
                <Button size="small" onClick={handleNext} disabled={activeStep === maxSteps - 1}>
                    หน้าถัดไป
                    {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
                </Button>
                }
                backButton={
                <Button size="small" onClick={handleBack} disabled={activeStep === 0}>
                    {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
                    หน้าก่อนหน้า
                </Button>
        }
      />)}
        </TabPanel>
        <TabPanel value={value} index={1} dir={theme.direction}>
        {teachingData && ( 
        <MultipleSelect data={teachingData} perpage={1} setRenderData={setRenderData} setActiveStep={setActiveStep}></MultipleSelect>
      )}
        {renderData && renderData.length>0 ? (
            renderData[activeStep].map((obj,index) => (
                <FilesCard theme={theme} type={1} obj={obj} index={index}></FilesCard>
          ))
            ):(
                <Typography gutterBottom variant="subtitle2" component="h6" color="textPrimary" style={{width: "100%", marginTop: theme.spacing(10)}}>
                      ไม่พบ {menu_items[32].title}
                    </Typography>
            )
        }
        {renderData && renderData.length>0 && (
        <MobileStepper
        style={{background: "transparent", backgroundColor: theme.palette.primary.main ,}}
                steps={maxSteps}
                position="static"
                variant="text"
                activeStep={activeStep}
                nextButton={
                <Button size="small" onClick={handleNext} disabled={activeStep === maxSteps - 1}>
                    หน้าถัดไป
                    {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
                </Button>
                }
                backButton={
                <Button size="small" onClick={handleBack} disabled={activeStep === 0}>
                    {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
                    หน้าก่อนหน้า
                </Button>
        }
      />)}
        </TabPanel>
        <TabPanel value={value} index={2} dir={theme.direction}>
        {plcData && ( 
        <MultipleSelect data={plcData} perpage={4} setRenderData={setRenderData} setActiveStep={setActiveStep}></MultipleSelect>
      )}
        {renderData && renderData.length>0 ? (
            renderData[activeStep].map((obj,index) => (
                <FilesCard theme={theme} type={0} obj={obj} index={index}></FilesCard>
          ))
            ):(
                
                <Typography gutterBottom variant="subtitle2" component="h6" color="textPrimary" style={{width: "100%", marginTop: theme.spacing(10)}}>
                      ไม่พบ {menu_items[33].title}
                    </Typography>
            )
        }
        {renderData && renderData.length>0 && (
        <MobileStepper
        style={{background: "transparent", backgroundColor: theme.palette.primary.main ,}}
                steps={maxSteps}
                position="static"
                variant="text"
                activeStep={activeStep}
                nextButton={
                <Button size="small" onClick={handleNext} disabled={activeStep === maxSteps - 1}>
                    หน้าถัดไป
                    {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
                </Button>
                }
                backButton={
                <Button size="small" onClick={handleBack} disabled={activeStep === 0}>
                    {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
                    หน้าก่อนหน้า
                </Button>
        }
      />)}
        </TabPanel>
      </SwipeableViews>
      
      
    </div>
  );
}