import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import ListSubheader from '@material-ui/core/ListSubheader';
import IconButton from '@material-ui/core/IconButton';
import InfoIcon from '@material-ui/icons/Info';
import { TransitionProps } from '@material-ui/core';

import { makeStyles,createStyles, useTheme } from '@material-ui/core/styles';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css';
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: 'flex',
      flexWrap: 'wrap',
      justifyContent: 'space-around',
      overflow: 'hidden',
      backgroundColor: theme.palette.background.paper,
      width: "98%",
    },
    gridList: {
      width: "100%",
      height: "60vh",
      overflow: "scroll"
    },
    title: {
      color: theme.palette.secondary.light,
    },
    titleBar: {
      background:
        'linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)',
    },
  }),
);

const Transition = React.forwardRef(function Transition(
  props: TransitionProps & { children?: React.ReactElement<any, any> },
  ref: React.Ref<unknown>,
) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function AlertDialogSlide({iopen, data, setIsCert}) {
  const [open, setOpen] = React.useState(iopen);
  const classes = useStyles();

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setIsCert(false);
  };
  const handleClickDownload = (event) => {
    window.open(event,'_blank');
}
  return (
    <div>
      <Dialog
        open={open}
        TransitionComponent={Transition}
        keepMounted
        onClose={handleClose}
        aria-labelledby="alert-dialog-slide-title"
        aria-describedby="alert-dialog-slide-description"
      >
        <DialogTitle id="alert-dialog-slide-title">{data[0].category}</DialogTitle>
        <DialogContent>
        <GridList cellHeight="10%" className={classes.gridList} cols={2}>
        {data.map((tile) => (
          <GridListTile key={tile.img} cols={tile.cols || 1}>
            { tile.picture.split(',').map((pic) => 
            <LazyLoadImage alt={tile.title} effect="blur" width="100%"
            src={pic.trim().replace("https://drive.google.com/open?id=", 
            "https://lh3.googleusercontent.com/d/").concat("=w600")}
            onClick={()=> {handleClickDownload(pic.trim())}}/> 
            )}
          </GridListTile>
        ))}
      </GridList>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            ปิด
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}