import React from "react";
import ReactDOM from "react-dom";

import axios from "axios";
import "react-image-gallery/styles/css/image-gallery.css";
import ImageGallery from "react-image-gallery";
import Paper from "@material-ui/core/Paper";
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import SkipPreviousIcon from '@material-ui/icons/SkipPrevious';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import SkipNextIcon from '@material-ui/icons/SkipNext';
import ShareIcon from '@material-ui/icons/Share';
 
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import { CardActionArea } from "@material-ui/core";
import {gallery_list} from "./data/personal_data";

import { Theme, createStyles, makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: 'flex',
      flexWrap: 'wrap',
      justifyContent: 'space-around',
      overflow: 'hidden',
      backgroundColor: theme.palette.background.paper,
    },
    gridList: {
      width: "100%",
      height: "60vh",
      overflow: "scroll"
    },
  }),
);
export default function ImagesGallery({index, theme}){
  const [images, setImages] = React.useState(null);
  const [open, setOpen] = React.useState(false);
  const [openImage, setOpenImage]  = React.useState(null);

  const classes = useStyles();

  const handleClickOpen = (event) => {
    setOpen(true);
    console.log(event);
    setOpenImage(event.target.src);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleClickDownloadG = (event) => {
    window.open(event,'_blank');
}

  const handleClickDownloadT = (event) => {
    var str = event.target.src; 
    var sli = str.slice(0,str.length-4);
    var res = "w2048";
    window.open(sli.concat(res),'_blank');
    console.log(res);
}


  const handleClickDownload = (event) => {
    var str = event.target.src; 
    var sli = str.slice(0,str.length-5);
    var res = "w2048";
    window.open(sli.concat(res),'_blank');
    console.log(res);
}


  React.useEffect(() => {
    let shouldCancel = false;

    const call = async () => {
      const response = await axios.get(
        gallery_list[index]
      );
      if (!shouldCancel && response.data && response.data.length > 0) {
        setImages(
          response.data.map(url => ({
            original: `${url}=w1024`,
            thumbnail: `${url}=w100`,
            hiThumbnail: `${url}=w400`
          }))
        );
      }
    };
    call();
    return () => (shouldCancel = true);
  }, []);


  return (images ? 
    <Card style={{width: "98%", alignItems: "center", height: "65vh", backgroundColor: theme.palette.secondary.light}} >
      <CardActionArea >
      <ImageGallery items={images} 
    onClick={handleClickOpen}
    showPlayButton={false} autoPlay={true}
    showFullscreenButton={false}
    showNav={false} />

    <Dialog style={{width: "100%"}}
        open={open}
        onClose={handleClose}
      >
      <CardActionArea >
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
          <IconButton aria-label="share">
          <Typography variant="subtitle1" color="textSecondary">
            คลิกที่รูปภาพเพื่อดาวน์โหลด
          </Typography>
        </IconButton> 
        <GridList cellHeight={160} className={classes.gridList} cols={3}>
        {images.map((tile) => (
          <GridListTile key={tile.original} cols={tile.cols || 1} 
          onClick={()=>{handleClickDownloadG(tile.original)}}>
            <img src={tile.original} alt="" />
          </GridListTile>
        ))}
      </GridList>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary" autoFocus>
            ปิด
          </Button>
        </DialogActions>
        </CardActionArea>
      </Dialog>

      </CardActionArea>
      </Card> : null);
}
