import React, {useEffect} from 'react';
import { Theme, createStyles, makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import IconButton from '@material-ui/core/IconButton';
import StarBorderIcon from '@material-ui/icons/StarBorder';
import OpenInNewIcon from '@material-ui/icons/OpenInNew';
import {menu_items} from './renderdata';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css';
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: 'flex',
      flexWrap: 'wrap',
      justifyContent: 'space-around',
      overflow: 'hidden',
      backgroundColor: theme.palette.background.paper,
      width: "98%",
    },
    gridList: {
      flexWrap: 'nowrap',
      // Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
      transform: 'translateZ(0)'
    },
    title: {
      color: theme.palette.secondary.light,
    },
    titleBar: {
      background:
        'linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)',
    },
  }),
);

/**
 * The example data is structured as follows:
 *
 * import image from 'path/to/image.jpg';
 * [etc...]
 *
 * const tileData = [
 *   {
 *     img: image,
 *     title: 'Image',
 *     author: 'author',
 *   },
 *   {
 *     [etc...]
 *   },
 * ];
 */
export default function SingleLineGridList({tileData, cols}) {
  const classes = useStyles();
  const [selectedData, setSelectedData] = React.useState([]);
  useEffect(() => {
    // Run! Like go get some data from an API.
    
  }, []);
  const handleClickDownload = (event) => {
    window.open(event,'_blank');
}
  return (
    <div className={classes.root}>
      <GridList className={classes.gridList} cols={cols}>
        {tileData.map((tile) => 
            tile.picture.split(',').map((pic) => 
          <GridListTile key={tile.title} style={{
            height: "100%"}}>
                <LazyLoadImage alt={tile.title} effect="blur" width="100%"
                src={pic.trim().replace("https://drive.google.com/open?id=", 
                "https://lh3.googleusercontent.com/d/").concat("=w600")}/> 
            <GridListTileBar
              title={tile.title}
              classes={{
                root: classes.titleBar,
                title: classes.title,
              }}
              actionIcon={
                <IconButton aria-label={`star ${tile.title}`} onClick={()=> {handleClickDownload(pic)}}>
                  <OpenInNewIcon className={classes.title} />
                </IconButton>
              }
            />
          </GridListTile>
        ))}
      </GridList>
    </div>
  );
}