import React from 'react';
import { createStyles, Theme, withStyles, WithStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import { TransitionProps } from '@material-ui/core';
import Slide from '@material-ui/core/Slide';
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import FavoriteCard from "./FavoriteCard";
import MultipleSelect from "./MultipleSelect";


const Transition = React.forwardRef(function Transition(
    props: TransitionProps & { children?: React.ReactElement<any, any> },
    ref: React.Ref<unknown>,
  ) {
    return <Slide direction="up" ref={ref} {...props} />;
  });

const styles = (theme: Theme) =>
  createStyles({
    root: {
      margin: 0,
      padding: theme.spacing(2),
      backgroundColor: theme.palette.primary.dark,
    },
    closeButton: {
      position: 'absolute',
      right: theme.spacing(1),
      top: theme.spacing(1),
      color: theme.palette.grey[500],
    },
  });

export interface DialogTitleProps extends WithStyles<typeof styles> {
  id: string;
  children: React.ReactNode;
  onClose: () => void;
}

const DialogTitle = withStyles(styles)((props: DialogTitleProps) => {
  const { children, classes, onClose, data, setRenderData, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}
          <MultipleSelect data={data} setRenderData={setRenderData}></MultipleSelect></Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme: Theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

const DialogActions = withStyles((theme: Theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(1),
  },
}))(MuiDialogActions);

export default function CustomizedDialogs({theme, title, data, isOpen,setOpenDialog}) {
  const [open, setOpen] = React.useState(isOpen);
  const [renderData, setRenderData] = React.useState([]);

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
    setOpenDialog(false);
  };

  return (
    <div>
      <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" 
      open={open}
      fullScreen={true}
      TransitionComponent={Transition}
      keepMounted>
        <DialogTitle id="customized-dialog-title" onClose={handleClose} data={data} setRenderData={setRenderData} >
          {title}
        </DialogTitle>
        <DialogContent dividers>
        <Grid item xs={12} sm={12} container spacing={1}>
      { renderData &&  (
        renderData.map((obj, index) => 
          
          <Grid item xs={12} sm={6} md={4}>
                <Paper style={{height: "100%"}}>
                <FavoriteCard type={0} obj={obj} index={index} limit={1000}></FavoriteCard>

                </Paper>
                
          </Grid>
      ))}
    </Grid>
        </DialogContent>
       {/* <DialogActions>
          <Button autoFocus onClick={handleClose} color="primary">
            Save changes
          </Button>
       </DialogActions> */}
      </Dialog>
    </div>
  );
}